@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&family=Acme&family=Kaushan+Script&family=Architects+Daughter&family=Overpass&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loader{
  position: fixed;
 width: 100vw;
  height: 100vh;
  object-fit: cover;
} 
.loading {
  display: block;
  width: 100%;
  height: 50rem;
  /* margin-bottom: 50rem; */
}
